@import url(https://fonts.googleapis.com/css2?family=Kumbh+Sans:wght@300;400;700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Baloo+Thambi+2:wght@700&display=swap);

body{padding:0px;margin:0px;color:#1F1F1F;font-family:'Kumbh Sans', sans-serif}.page{background:white;height:100%;width:100%;min-height:100vh}.content{max-width:800px;margin-left:auto;margin-right:auto;box-sizing:border-box;padding:20px;font-weight:300;font-size:.8rem;background:#f7f7f7;flex-grow:1;padding-top:70px}h1{color:#6f4ae0}a{color:inherit;text-decoration:inherit}strong{color:#6f4ae0;font-weight:bold}.header{width:100%;height:50px;background:linear-gradient(to right, #6f4ae0, #6f4ae0, #4a00e0);display:flex;align-items:center;justify-content:center;position:fixed;z-index:10000000}.footer{width:100%;height:70px;margin-top:auto;background:linear-gradient(to right, #6f4ae0, #6f4ae0, #4a00e0);display:flex;align-items:center;justify-content:space-around;font-size:12px;box-sizing:border-box;padding:0px 20px 0px 20px;color:white}.title{font-weight:bold;font-size:26px;color:white;font-family:'Baloo Thambi 2', sans-serif}.btn:active{transform:scale(0.95);opacity:.5}.home-content{min-height:calc(100vh - 70px);max-width:100%}.carousel-element{display:flex;align-items:center;justify-content:center;width:100%}.carousel-img{max-width:45vh}.carousel .slide{background-color:transparent}.control-arrow{color:#6f4ae0}.home-bottom-container{width:100%;display:flex;align-items:center}.home-bottom{box-sizing:border-box;padding:20px;width:100%;height:100%;display:flex;justify-content:center;align-items:center}.app-btn{max-width:80%}.carousel-title-wrap{width:100%}@media only screen and (max-width: 500px){.content{padding:10px;padding-top:60px}.carousel-container{height:100%}.carousel-title-wrap{width:50%}.carousel-title{font-size:16px}.home-bottom-container{flex-wrap:wrap;margin-top:auto;margin-bottom:auto;height:-webkit-fit-content;height:-moz-fit-content;height:fit-content}.app-btn{min-width:250px;max-width:50%}}

